aside {
  box-sizing: border-box;
  background-color: #252526;
}
aside > header {
  display: flex;
  height: 36px;
  line-height: 36px;
  margin: 0 8px;
  font-family: Segoe UI;
  font-size: 11px;
  font-weight: normal;
  color: #bbbbbb;
  text-transform: uppercase;
  user-select: none;
  cursor: default;
}
aside > header .title {
  flex-grow: 1;
  padding-left: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
aside > header > .actions > .action {
  height: 35px;
  line-height: 35px;
}



